import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Customer } from '../interfaces/customer.model';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icClose from '@iconify/icons-ic/twotone-close';
import icPrint from '@iconify/icons-ic/twotone-print';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icPerson from '@iconify/icons-ic/twotone-person';
import icMyLocation from '@iconify/icons-ic/twotone-my-location';
import icLocationCity from '@iconify/icons-ic/twotone-location-city';
import icEditLocation from '@iconify/icons-ic/twotone-edit-location';

import { NgForm } from '@angular/forms';
import { ClientService } from '../../../../shared/services/client.service';
import { Client } from '../../../../shared/models/client';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { firestore } from 'firebase';
import { StorageService } from '../../../../shared/services/storage/storage.service';

declare var toastr: any;
declare var $: any;
@Component({
  selector: 'vex-customer-create-update',
  templateUrl: './customer-create-update.component.html',
  styleUrls: ['./customer-create-update.component.scss']
})
export class CustomerCreateUpdateComponent implements OnInit {

  clienteWeb: Client = new Client();
  clientList: Client[];
  clientObject: Client;
  loading = false;

  static id = 100;

  form: FormGroup;
  mode: 'create' | 'update' = 'create';

  icMoreVert = icMoreVert;
  icClose = icClose;

  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;

  icPerson = icPerson;
  icMyLocation = icMyLocation;
  icLocationCity = icLocationCity;
  icEditLocation = icEditLocation;
  icPhone = icPhone;
  client: any;

  
  constructor(
    @Inject(MAT_DIALOG_DATA) 
    public defaults: any,
    private dialogRef: MatDialogRef<CustomerCreateUpdateComponent>,
    private fb: FormBuilder,
    private firestore: AngularFirestore,
    private storageService: StorageService,
    private customersService: ClientService
    ) {}

  ngOnInit() {
    if(this.storageService.getSession("$key")!=null){
      this.clienteWeb = this.storageService.getSession("mydata")     
    }

    if (this.defaults) {
      this.mode = 'update';
    } else {
      this.defaults = {} as Client;
    }

    const newId = this.firestore.createId();
    this.storageService.setSession("mykey", newId)

    this.form = this.fb.group({

      $key: newId,
      id: [CustomerCreateUpdateComponent.id++],
      //imageSrc: this.defaults.imageSrc,
      nombre: [this.defaults.nombre || ''],
      apellidos: [this.defaults.apellidos || ''],
      direccion: this.defaults.direccion || '',
      ciudad: this.defaults.ciudad || '',
      codigoPostal: this.defaults.codigoPostal || '',
      //idListaPrecio: this.defaults.idListaPrecio || '',
      //idRuta: this.defaults.idRuta || '',
      telefono: this.defaults.telefono || '',
      email: this.defaults.email || '',
      dia: this.defaults.dia || '',
      //latitud: this.defaults.latitud || '',
      //longitud: this.defaults.longitud || '',
      referencia: this.defaults.referencia || '',
    });
  }

  save(clientForm: NgForm) {
    if (this.mode === 'create') {
      this.addClient(clientForm);
    } else if (this.mode === 'update') {
      this.updateCustomers();
    }
  }

  
  addClient(clientForm: NgForm) {
    this.customersService.postClient(clientForm.value);
    this.createCustomer();
    alert("cliente guardado")

  }
  
  updateCustomers(){
    if(this.storageService.getSession("$key")!=null){
      //var value = new AddEstablishment
        var values = { 
          nombre:  this.clienteWeb.nombre,
          apellidos: this.clienteWeb.apellidos,    
          direccion: this.clienteWeb.direccion,
          codigoPostal: this.clienteWeb.codigoPostal,
          dia: this.clienteWeb.dia,
          ciudad: this.clienteWeb.ciudad,
          telefono: this.clienteWeb.telefono,
          email: this.clienteWeb.email,
          referencia: this.clienteWeb.referencia,

        }
  
        const customer = this.form.value;  
        customer.nombre = String(values.nombre)
        customer.apellidos = String(values.apellidos)
        customer.direccion = String(values.direccion)
        customer.codigoPostal = String(values.codigoPostal)
        customer.dia = String(values.dia)
        customer.ciudad= String(values.ciudad)
        customer.telefono = String(values.telefono)
        customer.email= String(values.email) 
        customer.referencia=String(values.referencia)  
        this.customersService.updateClient(values)
        this.updateCustomer()
        console.log('print value::'+ JSON.stringify(customer));
      }else{
        console.log('no entro')
      }
  }

  createCustomer() {
    const customer = this.form.value;

    if (!customer.imageSrc) {
      customer.imageSrc = 'assets/img/avatars/1.jpg';
    }

    this.dialogRef.close(customer);
  }

  updateCustomer() {
    const customer = this.form.value;
    customer.id = this.defaults.id;

    this.dialogRef.close(customer);
  }

  isCreateMode() {
    return this.mode === 'create';
  }

  isUpdateMode() {
    return this.mode === 'update';
  }
}
